@import '--size--m/complete-alert--size--m.scss';
@import '--size--s/complete-alert--size--s.scss';
.complete-alert {
    width: 473px;
    height: 90px;
    background: #b0fc72;
    border-radius: 0px 4px 4px 0px;
    margin-left: -100px;
    margin-bottom: 20px;
    display: none;
    align-items: center;
    justify-content: center;
    animation-duration: 0.5s;
    animation-fill-mode: both;
    animation-name: fadeInDown;
}

@keyframes fadeInDown {
    0% {
        opacity: 0;
        transform: translate3d(-100%, 0px, 0px);
    }
    100% {
        opacity: 1;
        transform: none;
    }
}